.form-container {
  position: absolute;
  top: 0;
  height: 100%;
  transition: all 0.6s ease-in-out;
/*   width: 100%; */
}

.sign-in-container {
  left: 0;
  width: 50%;
  z-index: 2;
  
}

.sign-up-container {
  left: 0;
  width: 50%;
  z-index: 1;
  opacity:0;
}

.overlay-container {
  position: absolute;
  top: 0;
  left: 50%;
  width: 50%;
  height: 100%;
  overflow: hidden;
  transition: transform 0.6s ease-in-out;
  z-index: 100;
}

.overlay {
  
  /* background: linear-gradient(to right, #ff4b2b, #ff416c) no-repeat 0 0 / cover ; */
  background-image: url("../../public/image/regi.gif");
  background-position: center;
  background-size: contain;
  /* color: #fff; */
  /* position: relative;
  left:-100%;*/
  height: 100%;
  width: 100%;
  background-repeat: no-repeat;
 
}

.overlay-panel {
  position: absolute;
  top: 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 40px;
  height: 100%;
  width: 50%;
  text-align: center;
  transform: translateX(0);
  transition: transform 0.6s ease-in-out;
}

.overlay-right {
  right: 0;
  transform: translateX(0);
}

.overlay-left {
  transform: translateX(-20%);
}


footer {
  width: 100%;
  height: 45px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: fixed;
  bottom: 0;
  background: #ff416c;
  background: linear-gradient(to right, #ff4b2b, #ff416c) no-repeat 0 0 / cover ;
  color: #fff;
  padding-bottom: -2px;
}

footer a {
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 0 5px;
  width: 40px;
  height: 40px;
  font-size: 25px;
}